.header__main {
  position: relative;
}

.background__img {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: max-content;
  overflow: hidden;
  z-index: 0;
}

.background__img img {
  width: 100%;
  height: 600px;
  object-fit: cover;
}

.header__main-content {
  display: inline-flex;
  flex-direction: column;
  align-items: flex-start;
  position: absolute;
  z-index: 1;
  padding-left: 6rem;
  top: 140px;
}

.title__text {
  font-family: var(--font-family1);
  color: #FFF;
  font-size: 48px;
  width: 404px;
  font-style: normal;
  font-weight: 700;
  line-height: 56px;
  display: flex;
  flex-wrap: wrap;
}

.ratings__content {
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: row;
  gap: 10px;
  margin-top: 16px;
}

.ratings__content img {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 16px;
}

.ratings {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 20px;
  font-family: var(--font-family1);
  color: #FFF;
  font-size: 13px;
  font-weight: 400;
  line-height: 12px;
}

.title__content {
  display: block;
  margin-top: 16px;
  width: 900px;
}

.title__content p {
  color: #FFF;
  font-family: var(--font-family1);
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.watch__btn {
  margin-top: 16px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.watchtrailer__btn {
  display: flex;
  padding: 6px 16px;
  align-items: center;
  gap: 8px;
  border-radius: 6px;
  background: var(--rose-700, #BE123C);
  outline: none;
  border: none;
  color: var(--white, #FFF);
  font-family: var(--font-family1);
  letter-spacing: 1px;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;
}

.watchtrailer__btn:hover {
  background: var(--rose-600, #9F1239);
  cursor: pointer;
}

/* RESPONSIVE STYLES */

@media screen and (max-width: 1024px) {
  .header__main-content {
    display: inline-flex;
    flex-direction: column;
    align-items: flex-start;
    position: absolute;
    z-index: 1;
    padding-left: 6rem;
    top: 230px;
  }

  .title__text {
    font-family: var(--font-family1);
    color: #FFF;
    font-size: 48px;
    width: 404px;
    font-style: normal;
    font-weight: 700;
    line-height: 56px;
    display: flex;
    flex-wrap: wrap;
  }

  .title__content {
    display: block;
    margin-top: 16px;
    width: 50%;
  }
}

@media screen and (max-width: 768px) {
  .header__main-content {
    display: inline-flex;
    flex-direction: column;
    align-items: flex-start;
    position: absolute;
    z-index: 1;
    padding-left: 6rem;
    top: 230px;
  }

  .title__text {
    font-family: var(--font-family1);
    color: #FFF;
    font-size: 48px;
    width: 404px;
    font-style: normal;
    font-weight: 700;
    line-height: 56px;
    display: flex;
    flex-wrap: wrap;
  }

  .title__content {
    display: block;
    margin-top: 16px;
    width: 80%;
  }
}

@media screen and (max-width: 480px) {
  .header__main-content {
    display: inline-flex;
    flex-direction: column;
    align-items: flex-start;
    position: absolute;
    z-index: 1;
    padding-left: 2rem;
    top: 200px;
  }

  .title__text {
    font-family: var(--font-family1);
    color: #FFF;
    font-size: 40px;
    width: 404px;
    font-style: normal;
    font-weight: 700;
    line-height: 56px;
    display: flex;
    flex-wrap: wrap;
  }

  .title__content {
    display: block;
    margin-top: 16px;
    width: 90%;
  }
}
