.sidebar__container{
    width: 100%;
    border-radius: 0px 45px 45px 0px;
    border: 1px solid rgba(0, 0, 0, 0.30);
    background: transparent;
      display: flex;
      padding: 20px;
      flex-direction: column;
      align-items: center;
      justify-content: space-evenly;
    
    
}

.sidebar__main{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap : 20px;
    
}

.sidebar__logo{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 24px;
    width: auto;
    font-family: var(--font-family2);
    padding-top: 10px;
    line-height: normal;
    cursor: pointer;
}

.sidebar__logo img{
    width: 50px;
    height: 50px;
}

.sidebar__logo span{
    color: #333;
    font-family: var(--font-family1);
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 24px; 
    letter-spacing: 1px;
}


.sidebar-content {
    flex: 1;
    padding: 20px 0 20px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }
  
  .sidebar__links {
   
   display: flex;
   flex-direction: column;
   justify-content: flex-start;
   align-items: baseline;
   gap: 45px;
  }


  
  .sidebar-li {
   
   display: flex;
   align-items: center;
   justify-content: center;
   gap: 10px;
   width: auto;
   list-style-type: none;
    color: #666;
    font-family: var(--font-family2);
    font-size: 20px;
    letter-spacing: 1px;
    margin-top: 20px;
    padding-top: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    cursor: pointer;
  }

  .sidebar-li-active{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    width: auto;
    list-style-type: none;
     color: #666;
     font-family: var(--font-family2);
     font-size: 20px;
     letter-spacing: 1px;
     margin-top: 20px;
     padding-top: 20px;
     font-style: normal;
     font-weight: 600;
     line-height: normal;
     cursor: pointer;
     border: 1px solid red;
   
  }

  .sidebar-li:hover {
   color: black;
    transition: 0.5s ease-in-out;
    font-size: 20.5px;
  }
  
  .sidebar__card-card{
    border-radius: 20px;
    border: 1px solid rgba(190, 18, 60, 0.70);
    background: rgba(248, 231, 235, 0.40);
    padding: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
  }

  .sidecardbody {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 1rem 0 1rem 0;
    
    
    
  }

  .sidecard__text{
    color: rgba(51, 51, 51, 0.80);
    font-family: var(--font-family2);
    font-size: 15px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
  }

  .sidebar__text2{
    color: rgba(51, 51, 51, 0.80);
    font-family: var(--font-family2);
    font-size: 15px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
  }

  .sidecard__btn{
    outline: none;
    border: none;
    border-radius: 30px;
    background: rgba(190, 18, 60, 0.20);
    align-items: center;
    color: #BE123C;
    font-family: var(--font-family2);
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    width: 112px;
    height: 30px;

  }

  .sidebar_li-logout{
    display: flex;
   align-items: center;
   justify-content: center;
   gap: 10px;
   width: auto;
  list-style-type: none;
    color: #666;
    font-family: var(--font-family2);
    font-size: 20px;
    letter-spacing: 1px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    cursor: pointer;
    margin-bottom: 2rem;
  }
  
  .sidebar_li-logout:hover {
    color: black;
     transition: 0.5s ease-in-out;
     font-size: 20.5px;
   }




