a {
  text-decoration: none;
}

.navbar__nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 2rem 6rem;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
}

.navbar__links {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.navbar__logo {
  display: flex;
  align-items: center;
  margin-right: 15rem;
}

.navbar__logo img {
  width: 100%;
  height: 100%;
}

.navbar__center {
  display: flex;
  align-items: center;
  margin-right: 2rem;
  justify-content: center;
  border: 2px solid var(--gray-300, #D1D5DB);
  border-radius: 6px;
}

.navbar__center button {
  border: none;
  outline: none;
  color: transparent;
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 5px;
}

.navbar__searchbar {
  font-family: var(--font-family1);
  letter-spacing: 1px;
  border: none;
  outline: none;
  display: flex;
  width: 525px;
  height: 40px;
  padding: 6px 10px;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  color: var(--white, #FFF);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

input::placeholder {
  color: white;
}

/* Sign In Button */
.navbar__right {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-left: 12.1rem;
}

.sign__in-btn {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  background-color: transparent;
  font-family: var(--font-family1);
  padding: 8px 16px;
  border: none;
  cursor: pointer;
  color: #FFF;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
}

.sign__in-btn2 {
  width: 36px;
  height: 36px;
  display: flex;
  justify-content: center;
  border-radius: 50%;
  background-color: #be123c;
  outline: none;
  border: none;
  align-items: center;
}

/* Responsiveness */
@media screen and (max-width: 1024px) {
  .navbar__nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 2rem 6rem;
  }

  .navbar__links {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
  }

  .navbar__logo {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-right: 5rem;
  }

  .navbar__center {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid var(--gray-300, #D1D5DB);
    border-radius: 6px;
    width: 60%;
  }

  .navbar__center button {
    margin-right: 0.5rem;
  }

  .navbar__searchbar {
    width: 100%;
    align-items: center;
    display: flex;
    justify-content: center;
  }

  .navbar__right {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: flex-end;
  }
}

@media screen and (max-width: 768px) {
  .navbar__nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .navbar__links {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 1rem;
    margin-left: -10rem;
  }

  .navbar__logo {
    width: auto;
    height: auto;
    margin-left: -9.5rem;
  }

  .navbar__logo img {
    width: 70%;
    height: 70%;
  }

  .navbar__center {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 400px;
    margin-top: 2rem;
    margin-left: 6rem;
    margin-right: -6rem;
    height: fit-content;
  }

  .navbar__center button {
    margin-right: 0px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }

  .navbar__searchbar {
    width: 100%;
    margin-right: 0;
    align-items: center;
    display: flex;
    justify-content: center;
  }

  .navbar__right {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: flex-end;
    margin-right: -30rem;
    margin-top: -7.3rem;
  }
}

@media screen and (max-width: 480px) {
  .navbar__nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 2rem 2em;
  }

  .navbar__links {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .navbar__logo {
    width: auto;
    height: auto;
    margin-left: 6rem;
  }

  .navbar__logo img {
    width: 70%;
    height: 70%;
  }

  .navbar__center {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin-top: 2rem;
    height: fit-content;
    margin-bottom: 2rem;
  }

  .navbar__searchbar {
    width: 100%;
    margin-right: 0;
    align-items: center;
    display: flex;
    justify-content: center;
  }

  .navbar__right {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: flex-end;
    margin-right: -15rem;
    margin-top: -9rem;
  }
}
