.footer__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 36px;
  }
  
  .social_icon {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 48px;
  }
  
  .footer__text {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    gap: 48px;
  }
  
  .footer__text p {
    color: var(--gray-900, #111827);
    font-family: var(--font-family1);
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
  
  .footer__copyright {
    display: flex;
    flex-direction: row;
    justify-content: center;
    color: var(--gray-500, #6B7280);
  }
  
  .footer__copyright p {
    color: var(--gray-500, #6B7280);
    font-family: var(--font-family1);
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
  
  @media screen and (max-width: 480px) {
    .footer__container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 36px;
      padding: 2rem 6rem;
    }
  
    .footer__copyright p {
      color: var(--gray-500, #6B7280);
      font-family: var(--font-family1);
      font-size: 15px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
      width: 200px;
      border: 1px solid #6B7280;
    }
  }
  