.moviedetails__container {
    padding: 1rem 6rem;
    width: 100%;
    align-items: center;
  }
  
  .features {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
  }
  
  .features p {
    display: flex;
    align-items: center;
    margin-top: 13px;
    color: #000;
    font-family: var(--font-family1);
    font-size: 36px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    justify-content: start;
  }
  
  .features__cta {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  
  .features__cta a {
    color: var(--rose-700, #BE123C);
    font-family: var(--font-family1);
    font-size: 18px;
    font-style: normal;
    cursor: pointer;
    font-weight: 400;
    line-height: 24px;
    justify-content: flex-end; /* Change end to flex-end */
    align-items: center;
    display: flex;
  }
  
  .cards__container {
    display: grid;
    flex-wrap: wrap;
    justify-content: space-between;
    grid-template-columns: repeat(4, 1fr);
    padding: 2rem 0;
    width: 100%;
    gap: 80px;
  }
  
  .movie-card {
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    border-radius: 2px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    max-width: 350px;
    margin-bottom: 1rem;
    gap: 12px;
    cursor: pointer;
  }
  
  .movie-card:hover {
    transition: 0.3s ease-in-out;
    transform: scale(1.05);
  }
  
  .like-icon {
    margin-left: 10px;
    margin-top: 10px;
    cursor: pointer;
    position: absolute;
    color: #fff;
    font-size: 24px;
    transform: scale(1);
    transition: transform 0.2s ease-in-out, color 0.2s ease-in-out;
  }
  
  .like-icon:hover {
    transform: scale(1.2);
    color: var(--rose-700, #BE123C);
  }
  
  .like-icon.liked {
    color: var(--rose-700, #BE123C);
  }
  
  .movie-poster {
    max-width: 100%;
    height: auto;
  }
  
  .movie-info {
    text-align: left;
    gap: 12px;
    padding-top: 12px;
  }
  
  .movie-title {
    color: var(--gray-900, #111827);
    font-family: var(--font-family1);
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
  
  .movie-release-year {
    color: var(--gray-400, #9CA3AF);
    font-family: var(--font-family1);
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
  
  .movie-description {
    font-size: 1rem;
    margin: 8px 0;
  }
  
  .ratings__card-right {
    display: flex;
    align-items: center;
    margin-left: 0;
    justify-content: start;
    gap: 10px;
  }
  
  .ratings__card-left {
    display: flex;
    align-items: center;
    margin-left: 0;
    justify-content: end;
    margin-top: -40px;
    gap: 10px;
  }
  
  .cardratings {
    color: var(--gray-900, #111827);
    font-family: var(--font-family1);
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 12px;
    margin-top: 15px;
  }
  
  .movie-genre {
    color: var(--gray-400, #9CA3AF);
    font-family: var(--font-family1);
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
  
  @media screen and (max-width: 1024px) {
    .cards__container {
      display: grid;
      flex-wrap: wrap;
      justify-content: space-between;
      grid-template-columns: repeat(3, 1fr);
      padding: 2rem 0;
      width: 100%;
      gap: 30px;
    }
  }
  
  @media screen and (max-width: 768px) {
    .cards__container {
      display: grid;
      flex-wrap: wrap;
      justify-content: space-between;
      grid-template-columns: repeat(2, 1fr);
      padding: 2rem 0;
      width: 100%;
      gap: 30px;
    }
  }
  
  @media screen and (max-width: 480px) {
    .cards__container {
      display: grid;
      flex-wrap: wrap;
      justify-content: space-between;
      grid-template-columns: repeat(1, 1fr);
      padding: 2rem 0;
      width: 100%;
      gap: 40px;
    }
  
    .features p {
      font-size: 16px;
    }
  
    .features__cta a {
      font-size: 14px;
    }
  }
  