.search-results {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 2rem 0;
    gap: 2rem;
    background: linear-gradient(to bottom, #232323, #f5f5f5);
  }
  
  .cards__search {
    display: grid;
    flex-wrap: wrap;
    justify-content: space-between;
    grid-template-columns: repeat(4, 1fr);
    padding: 6rem;
    width: 100%;
    gap: 80px;
  }
  
  .movie-card {
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    border-radius: 2px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    max-width: 350px;
    margin-bottom: 1rem;
    gap: 12px;
    cursor: pointer;
  }
  
  .movie-poster {
    max-width: 100%;
    height: auto;
  }
  
  .movie-info {
    text-align: left;
    gap: 12px;
    padding-top: 12px;
  }
  
  .movie-title {
    color: var(--gray-900, #111827);
    font-family: var(--font-family1);
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
  
  .movie-release-year {
    color: var(--gray-400, #9CA3AF);
    font-family: var(--font-family1);
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
  
  .movie-description {
    font-size: 1rem;
    margin: 8px 0;
  }
  
  .ratings__card-right {
    display: flex;
    align-items: center;
    margin-left: 0;
    justify-content: start;
    gap: 10px;
  }
  
  .ratings__card-left {
    display: flex;
    align-items: center;
    margin-left: 0;
    justify-content: flex-end;
    margin-top: -40px;
    gap: 10px;
  }
  
  .cardratings {
    color: var(--gray-900, #111827);
    font-family: var(--font-family1);
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 12px;
    margin-top: 15px;
  }
  
  .movie-genre {
    color: var(--gray-400, #9CA3AF);
    font-family: var(--font-family1);
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
  
  @media screen and (max-width: 1024px) {
    .cards__search {
      display: grid;
      flex-wrap: wrap;
      justify-content: space-between;
      grid-template-columns: repeat(3, 1fr);
      padding: 12rem 6rem;
      width: 100%;
      gap: 30px;
    }
  }
  
  @media screen and (max-width: 768px) {
    .cards__search {
      display: grid;
      flex-wrap: wrap;
      justify-content: space-between;
      grid-template-columns: repeat(2, 1fr);
      padding: 12rem 6rem;
      width: 100%;
      gap: 40px;
    }
  }
  
  @media screen and (max-width: 480px) {
    .cards__search {
      display: grid;
      flex-wrap: wrap;
      justify-content: space-between;
      grid-template-columns: repeat(1, 1fr);
      padding: 12rem 6rem;
      width: 100%;
      gap: 20px;
    }
  }
  