.searchresult__container {
    display: flex;
    flex-direction: row;
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
  }
  
  .searchbody__container {
    padding: 40px;
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  
  .trailer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    width: 100%;
  }
  
  .card {
    border-radius: 20px;
    outline: none;
    border: none;
  }
  
  .card img {
    border-radius: 20px;
    object-fit: cover;
    overflow: hidden;
  }
  
  .card-img {
    object-fit: contain;
    width: 1198px;
    height: 449px;
    border-radius: 20px;
  }
  
  .card-img-overlay {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
    position: relative;
    bottom: 300px;
  }
  
  .play__icon {
    color: #e8e8e8;
    cursor: pointer;
  }
  
  .card-title {
    color: #e8e8e8;
    text-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
    font-family: var(--font-family2);
    font-size: 25px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    cursor: pointer;
  }
  
  .searchbody__info-heading {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 10px 30px 10px 30px;
  }
  
  .searchbody__title {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 10px;
    color: #404040;
    font-family: var(--font-family2);
    font-size: 23px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
  
  .searchbody__genre {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 29px;
    color: #b91c1c;
    font-family: var(--font-family2);
    font-size: 15px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    margin-right: 1rem;
  }
  
  .searchbody__genre span {
    display: flex;
    justify-content: center;
    border-radius: 15px;
    border: 1px solid #f5dae0;
    width: 84px;
    height: 30px;
    align-items: center;
  }
  
  .searchbody__ratings {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 10px;
    font-family: var(--font-family2);
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }
  
  .searchbody__info {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 10px 30px 10px 30px;
    gap: 10px;
  }
  
  .searchbody__info-left {
    display: flex;
    flex-direction: column;
    gap: 15px;
    width: 700px;
  }
  
  .searchbody__description {
    color: #333;
    font-family: var(--font-family2);
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    align-items: center;
    text-align: start;
  }
  
  .searchbody__credits {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: baseline;
    gap: 10px;
  }
  
  .searchbody__credits-director,
  .searchbody__credits-writer,
  .searchbody__credits-cast {
    color: #333;
    font-family: var(--font-family2);
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    margin-bottom: 1rem;
  }
  
  .searchbody__credits-director span,
  .searchbody__credits-writer span,
  .searchbody__credits-cast span {
    color: #b91c1c;
    font-family: var(--font-family2);
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }
  
  .searchbody__extramenu {
    display: flex;
    flex-direction: row;
    border-radius: 10px;
    border: 1px solid #c7c7c7;
    background: rgba(255, 255, 255, 0.80);
    align-items: center;
    justify-content: space-between;
    margin-top: 1rem;
  }
  
  .searchbody__extramenu-left {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: start;
    background-color: #be123c;
    border: none;
    border-radius: 10px;
    outline: none;
  }
  
  .searchbody__extramenu-title {
    color: #fff;
    font-family: var(--font-family2);
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    margin: 10px;
    align-items: center;
    display: flex;
    justify-content: center;
  }
  
  .searchbody__extramenu-right {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 10px;
    margin-right: 9rem;
  }
  
  .searchbody__extramenu-icon {
    margin: 10px;
  }
  
  .searchbody__extramenu-title2 {
    color: #333;
    font-family: var(--font-family2);
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin: 10px;
    align-items: center;
    display: flex;
  }
  
  .searchbody__info-right {
    display: flex;
    flex-direction: column;
    gap: 12px;
    justify-content: start;
    align-items: center;
  }
  
  .searchbody__info-right-top {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 1px solid #be123c;
    border-radius: 10px;
    padding: 0 1rem;
    cursor: pointer;
    background-color: #be123c;
    width: 100%;
    height: 55px;
  }
  
  .searchbody__info-right-top-btn1 {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 10px;
    width: 100%;
    border: none;
    outline: none;
    background-color: transparent;
    color: #fff;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    margin: 10px;
    align-items: center;
    display: flex;
    justify-content: center;
  }
  
  .searchbody__info-right-top2 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 1px solid #be123c;
    border-radius: 10px;
    padding: 0 1rem;
    cursor: pointer;
    width: 100%;
    height: 55px;
  }
  
  .searchbody__info-right-top-btn2 {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    gap: 10px;
    width: 100%;
    border: none;
    outline: none;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    margin: 10px;
    align-items: center;
    display: flex;
    justify-content: center;
  }
  
  .card-info-right-bottom {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 0.4rem;
    width: 100%;
    border-radius: 10px;
  }
  
  .card-2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-self: center;
  }
  
  .card-2 img {
    position: absolute;
    object-fit: contain;
    overflow: hidden;
  }
  
  .card-img2-overlay {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  
  .searchbody__bottom-card-btn {
    border-radius: 10px;
    backdrop-filter: blur(2px);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    outline: none;
    margin: 10px;
    outline: none;
    border: none;
    border: 2px solid yellow;
  }
  
  .searchbody__bottom-card-btn span {
    color: #620000;
    text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.25);
    font-family: var(--font-family2);
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }
  
  @media screen and (max-width: 768px) {
    .searchbody__container {
      padding: 40px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 100%;
      align-items: center;
    }
  
    .card-img-overlay {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      position: absolute;
      right: 10px;
    }
  
    .card {
      border-radius: 20px;
      outline: none;
      border: none;
      width: 100%;
      display: flex;
      align-items: center;
    }
  
    .card img {
      width: 100%;
      height: 100%;
    }
  
    .card-img {
      object-fit: contain;
      width: 100%;
      height: 100%;
      border-radius: 20px;
    }
  
    .searchbody__info-heading {
      display: flex;
      flex-direction: row;
      justify-content: center;
      margin-left: 0;
      align-items: center;
      flex-wrap: wrap;
      width: 100%;
      gap: 10px;
    }
  
    .searchbody__info {
      display: flex;
      flex-direction: column;
      margin-top: 1rem;
    }
  
    .searchbody__info-left {
      display: flex;
      flex-direction: column;
      gap: 15px;
      width: 100%;
    }
  
    .searchbody__description {
      width: fit-content;
      font-size: 18px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
    }
  
    .searchbody__info-right {
      display: flex;
      flex-direction: column;
      gap: 12px;
      justify-content: start;
      align-items: center;
      width: 100%;
      margin-left: 0;
      margin-top: 1rem;
    }
  }
  
  @media screen and (max-width: 480px) {
    .card-img-overlay {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      border: 1px solid red;
      position: absolute;
      left: 600px;
    }
  }
  